			})
		}
	},
	onLoad: function() {

		this.customNavBarH = 45

		
		this.getData()
	},
	onReady: function() {
		
	},
	methods: {
		// 分类数据加载失败的情况下 用户点击页面再次尝试请求并加载数据
		reloadData: function() {
			this.beforeReady = true
			this.getData()
		},
		
		getData() {
			this.cate = this.fakeCate
			this.list = this.fakeData
			return
			
			this.$api.get('/category/app_category').then(res => {
				let cate = []
				let list = []
				res.forEach(v => {
					cate.push({
						id: v.id,
						name: v.name
					})
					list.push({
						list: v.app_category_items
					})
				})
				this.cate = cate
				this.list = list
				
				this.beforeReady = false
				
				// 将回调函数延迟到下次DOM更新循环之后执行
				this.$nextTick(() => {
					// this.getAllDomInfo()
				})
			}).catch(err => {
				this.beforeReady = false
			})
		},
		
		// 左边分类点击事件
		changeCate: function(index) {
			this.activeIndex = index
		},

		openDetail: function(id) {
			uni.navigateTo({
				url: '/pages/detail/detail?goods_id=' + id
			});
		},
		openMessage: function() {
			uni.navigateTo({
				url: "/pages/msg-list/msg-list"
			})
		},
		openSearch: function() {
			uni.navigateTo({
				url: '/pages/search/search'
			})
		}
